<template>
  <form class="card auth-card" @submit.prevent="submitHandler">
    <div class="card-content">
    <h4> {{ $localizeFilter('password_restore') }} </h4>
      
      <div class="input-field" style="margin-top: 30px;">
        <label for="password"
          >{{ $localizeFilter('enter_new_password') }}<span class="required">*</span></label
        >
        <input
          id="password"
          type="password"
          v-model.trim="password"
          :class="{ invalid: v$.password.$error }"
        />
        <small
          class="error"
          v-for="(error, index) of v$.password.$errors"
          :key="index"
        >
          
          {{ error.$message }}
        </small>
      </div>
    </div>

    <div class="card-action">
      <div>
        <button class="blue-button" type="submit">
          <p class="button-title">{{ $localizeFilter('restore') }}</p>
          <i class="material-icons right">send</i>
        </button>
      </div>
    </div>
  </form>
</template>

<script>
import useVuelidate from "@vuelidate/core";
import { required, helpers } from "@vuelidate/validators";
import { defineComponent } from "@vue/composition-api";
import localizeFilter from "@/filters/localize.filter"

export default defineComponent({
  props: {
    token: {
      type: String,
      required: true
    }
  },
  setup() {
    return { v$: useVuelidate() };
  },
  data() {
    return {
      password: ""
    };
  },
  validations() {
    return {
      password: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required) },
    };
  },
  methods: {
    async submitHandler() {
      this.v$.$touch();
        if (this.v$.$error) {
          return;
        }
        try {
          await this.$store.dispatch('restorePassword', {newPassword: this.password, code: this.token})
          this.$message(localizeFilter('restore_password_successfull'))
          this.$router.push('/login')
        } catch (e) {
             this.$error(e)
        }
    },
  },
  computed: {
  },
});
</script>

